body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	text-align: center;
	margin: 20px 0;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

table {
	width: 100%;
}
.col-time {
	width: 25%;
}
.col-sl {
	width: 65px;
}
.col-actions {
	width: 100px;
}
.highlight-search {
	background: yellow;
}
.pd-t-10 {
	padding-top: 10px;
}
.search {
	margin-bottom: 20px;
}

.pagination-row span {
	padding: 0px 10px;
	border: 1px solid #4344bb;
}
.delete-btn {
	color: #dc3545;
	cursor: pointer;
}
.edit-btn {
	margin-right: 20px;
	cursor: pointer;
}
.search {
	margin-bottom: 20px;
}

.pagination-row span {
	padding: 1px 8px;
	border: 1px solid #ccc;
	margin: 0 3px;
	line-height: 1;
	border-radius: 3px;
	cursor: pointer;
}

.pagination-row span.active {
	background: #777;
	color: #fff;
	border: 1px solid #222;
	box-shadow: 0 1px 5px #ccc;
}

.modal-close-btn {
  background: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
  text-align: center;
  outline: none;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.8);
}
.modal-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 20px 0;
  box-sizing: border-box;
  z-index: 100;
}
.modal-container {
  width: 50%;
  background: #fff;
  z-index: 100;
  padding: 10px 20px;
  box-sizing: border-box;
  border-radius: 4px;
  box-shadow: 0 0 2px #ccc;
  max-height: 100%;
  overflow-y: auto;
  text-align: left;
}
.modal-header {
  justify-content: space-between;
  display: flex;
  margin: 0 0 20px;
}
.modal-header h1 {
  font-size: 1.2rem;
  margin: 0;
}
.modal-main-content {
  margin: 0 0 10px;
}

